.breadcrumbs-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  font-size: 14px;
  margin: 0 !important;
  padding: 0 !important;
}

.breadcrumbs {
  margin: 0 !important;
  padding: 0 !important;
}

.breadcrumbs-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

.breadcrumb-item {
  display: flex;
  align-items: center;
  line-height: 1.5;
  padding: 4px 0;
}

.separator {
  margin: 0 8px;
  color: #7f7f7f;
}

.breadcrumb-link,
.breadcrumb-text {
  color: #1a73e8;
  text-decoration: none;
  font-weight: bold;
  white-space: nowrap;
  line-height: 1.5;
  padding: 4px 0;
  display: inline-block;
}

.breadcrumb-link:hover {
  text-decoration: underline;
}

.breadcrumb-text {
  color: #202124;
  font-weight: bold;
  cursor: default;
}

.favorite-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  cursor: pointer;
  flex-shrink: 0;
  margin-left: 12px;
}

.favorite-icon svg {
  transition: opacity 0.2s ease;
}

.favorite-icon:hover svg {
  opacity: 0.8;
}

[data-testid="stVerticalBlock"] > div:has(> [data-testid="element-container"] > .stBreadcrumbs) {
  margin-left: 0 !important;
  margin-right: 0 !important;
}